.counter {
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 5px #0002;
}

.row {
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 1rem 0;
}

.col {
  width: 250px;
  background: #f5f5f5;
  border-radius: 5px;
  padding: 2rem 0;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
}

.col:hover{
  transform: translateY(-10px) scale(1.05);
  box-shadow: 2px 4px 30px rgba(0, 0, 0, 0.4);
}

.counter-count {
  font-size: 18px;
  background-color: #00b3e7;
  border-radius: 50%;
  position: relative;
  color: #ffffff;
  text-align: center;
  line-height: 92px;
  width: 92px;
  height: 92px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  display: inline-block;
  margin-bottom: 1rem;
}

.employee-p,
.customer-p,
.order-p,
.design-p {
  font-size: 1.2rem;
  color: var(--color-dark);
  line-height: 34px;
}