section.home {
  margin-top: 4rem;
  text-align: center;
}

.home__image {
  width: 200px;
  margin: 1rem auto;
}

.home__buttons {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.home__button {
  text-align: center;
}

.button__image {
  width: 40%;
  margin: auto;
  padding: 6rem 0;
  background: linear-gradient(45deg, #0f589c, #2387c5, #0f589c);
  border-radius: 10px;
}
.home__button:last-child .button__image {
  background: linear-gradient(45deg, #3d7cb8, #c239b4, #3d7cb8);
}

.button__image img {
  width: 50%;
}

.home__button .btn::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 0;
  bottom: 0;
  left: -100%;
  border-bottom: 2px solid var(--color-primary);
}
.home__button .btn::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 0;
  bottom: 0;
  right: -300%;
  border-bottom: 2px solid var(--color-primary);
  transition: 1s all ease;
}

.home__button:hover .btn::before {
  left: 0;
  transition: var(--transition);
}
.home__button:hover .btn::after {
  right: 0;
}


/* news */
.news h2 {
  margin-bottom: 0;
}

.news .nav {
  height: 50px;
}

.blogs {
  margin: 1rem 0;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  align-items: flex-end;
}

.blog {
  width: 30%;
  flex-grow: 1;
  border-radius: 10px;
  padding:  1rem;
  box-shadow: 2px 2px 5px #0003;
  transition: var(--transition);
  position: relative;
}
.blog:hover {
  transform: scale(1.005);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
}
.blog:hover img{
  transform: translateY(-10px);
}

.blog__image {
  width: 100%;
  height: 215px;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  display: grid;
  flex-grow: 1;
  grid-template-rows: 1fr 1fr;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
}
.blog__image img {
  position: absolute;
  top: 0;
  height: 110%;
  width: 100%;
  filter: opacity(0.8);
  z-index: -1;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
}

.blog__title {
  color: var(--color-primary);
  font-size: 1.2rem;
}
.blog__text {
  color: var(--color-dark-variant);
  font-weight: 400;
  font-size: 16px;
  align-self: end;
  letter-spacing: 0.4px;
}